                    :validations=\"validations.name\" />\n            </div>\n        </div>\n        <div class=\"row mt-4\">\n            <div class=\"col\">\n                <span class=\"input-text\">Role</span>\n                <CustomSelect class=\"select-input\" :options=\"userRolesOptions\" :multiple=\"true\"\n                    :defaultMultiple=\"rolesInput.map(item => { return { name: item } })\"\n                    placeholder=\"Select role\"\n                    multipleSelectedClass=\"role-tag-name\" @input=\"selectUserRoles\" />\n            </div>\n        </div>\n        <div class=\"row mt-4\">\n            <div class=\"col\">\n                <span class=\"input-text\">Swimlane</span>\n                <CustomSelect class=\"select-input\" :options=\"swimlaneOptions\" :multiple=\"true\"\n                placeholder=\"Select swimlane\"\n                    :defaultMultiple=\"swimlaneInput.map(item => { return { name: item } })\"\n                    multipleSelectedClass=\"select-input-tag-name\" @input=\"selectSwimlanes\" />\n            </div>\n        </div>\n    </div>\n    <CreateDialogFooter :cancel-button-cb=\"() => emit('close')\" :create-button-cb=\"create\"\n        :create-button-enabled=\"disableButton\" />\n</template>\n<script setup lang=\"ts\">\nimport { computed, onMounted, ref } from \"vue\";\nimport { container } from \"../../../../../domain/core/diContainer\";\nimport { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\nimport { IIDEInputDefinitions } from \"../../../../../domain/presentation/IIDEInputDefinitions\";\nimport IDEInput from \"../../../components/application/IDEInput.vue\";\nimport CreateDialogFooter from \"./createDialogFooter.vue/createDialogFooter.vue\";\nimport { ICreateModel, ICreateModelParam } from \"../../../../../domain/useCase/ICreateModel\";\nimport CustomSelect from \"../../customSelect.vue\";\n\ninterface ICreateNewModuleInline {\n    parentId: string;\n}\n\ninterface IOptions { name: string, value: string };\n\nconst props = defineProps<ICreateNewModuleInline>();\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n\nconst emit = defineEmits([\"close\"]);\n\nconst isUsernameValid = ref<boolean>(false);\nconst isUnitValid = ref<boolean>(false);\nconst userRolesOptions = ref<Array<IOptions>>([]);\nconst swimlaneOptions = ref<Array<IOptions>>([]);\nconst usernameInput = ref<string>(\"\");\nconst unitInput = ref<string>(\"\");\nconst rolesInput = ref<Array<string>>([]);\nconst swimlaneInput = ref<Array<string>>([]);\n\nconst workflow = window.workflow;\n\n\nconst validations = ref<Record<\"name\", IIDEInputDefinitions[\"validations\"]>>({\n    name: { minAndMaxLength: { min: 2, max: 40 }, availableCharacters: true, nonSpaceCharacter: true }\n});\nconst disableButton = computed(()=> isUsernameValid.value && isUnitValid.value && rolesInput.value.length > 0 && swimlaneInput.value.length > 0) \nconst name = ref(\"\");\n\nonMounted(async () => {\n        executor.execute(\n        async () => {\n            convertUserRoles();\n            convertSwimlanes();\n        },\n        { loading: true }\n    );\n});\n\nfunction convertUserRoles() {\n    userRolesOptions.value = workflow.listUserRoles().map((item:string) => { return { name: item, value: item } });\n}\n\nfunction convertSwimlanes() {\n    swimlaneOptions.value = workflow.listSwimlanes().map((item:string) => { return { name: item, value: item } });\n}\nfunction selectUserRoles(value: Array<IOptions>) {\n    rolesInput.value = value.map((item: IOptions)=> item.name);\n}\nfunction selectSwimlanes(value: Array<IOptions>) {\n    swimlaneInput.value = value.map((item: IOptions)=> item.name);\n}\nfunction setUsername(value: string) {\n    usernameInput.value = value;\n}\nfunction setUnit(value: string) {\n    unitInput.value = value;\n}\nfunction setUsernameValidation(isValid: boolean) {\n    isUsernameValid.value = isValid;\n}\nfunction setUnitValidation(isValid: boolean) {\n    isUnitValid.value = isValid;\n}\n\nfunction create() {\n    executor.execute(\n        async () => {\n            const findUser = { id: \"\", unit: unitInput.value, name: usernameInput.value, roles: rolesInput.value, swimlane: swimlaneInput.value }; //FIXME: define userRequestDTO without id?\n            await workflow.insertUser(findUser);           \n            emit(\"close\");\n        },\n        { loading: true }\n    );\n}\n\n\n\n</script>\n\n<style scoped>\n@import url(\"../../../../../styles/IDE3.css\");\n\n.select-input {\n    width: 100%;\n    font-size: 15px;\n    height: 40px;\n    line-height: 40px;\n    border: 1px solid #BCBCBC;\n    border-radius: 4px;\n    padding: 0px 12px;\n}\n.input-text {\n    color: #151233;\n    font-weight: bold;\n    font-size: 14px;\n}\n</style>\n<style>\n.select-input .items {\n    color: #212121;\n    border-radius: 8px;\n    border: 1px solid #E3E3E3;\n    overflow: hidden;\n    background-color: #FFFFFF;\n    box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n    margin-top: 8px;\n}\n\n.select-input .items .option {\n    color: #212121;\n    padding-left: 12px;\n    line-height: 32px;\n}\n\n.select-input .items .option:hover {\n    background-color: #EEEEEE;\n}\n\n.select-input i {\n    font-size: 16px;\n    color: #BCBCBC;\n}\n\n.select-input-tag-name {\n    align-items: center;\n    padding: 5px 8px;\n    background: #D0D1E9;\n    display: flex;\n    justify-content: center;\n    border-radius: 20px;\n    margin-right: 4px;\n    font-size: 14px;\n    color: #2F3160;\n    white-space: nowrap;\n    font-weight: 500;\n}\n</style>\n\n"],"sourceRoot":""}]);
